<template>
  <dt-scroll-box>
    <div slot="top">
      <div class="dt-page-top clearfix">
        <span class="dt-bread-item">项目评审</span>
      </div>
    </div>
    <div slot="main-content">
      <div class="project-review-page">
        <div class="search-box clearfix">
          <div class="fl">
            <div class="table-item pl0">
              <div class="search-item">
                <span>项目名称：</span>
                <el-select class="dt-select" clearable v-model="searchData.orgId"  placeholder="请选择">
                    <el-option
                      v-for="item in orgOptions"
                      :key="item.id"
                      :label="item.label"
                      :value="item.id">
                    </el-option>
                 </el-select>
              </div>
              <div class="search-item">
                <span>姓名：</span>
                <el-input class="dt-input" placeholder="请输入"></el-input>  
              </div>
            </div>
            <div class="pl80 table-item">
              <div class="search-item">
                <span>项目类型：</span>
                <el-select class="dt-select" clearable v-model="searchData.year" placeholder="请选择">
                    <el-option
                      v-for="item in yearOptions"
                      :key="item"
                      :label="item + '年'"
                      :value="item">
                    </el-option>
                 </el-select>
              </div>
              <div class="search-item">
                <span>依托单位：</span>
                <el-input class="dt-input" placeholder="请输入"></el-input>  
              </div>
            </div>
            <div class="pl80 table-item">
              <div class="search-item">
                <span>项目状态：</span>
                <el-select class="dt-select" clearable v-model="searchData.month"  placeholder="请选择">
                    <el-option
                      v-for="i in 12"
                      :key="i"
                      :label="i + '月'"
                      :value="i + ''">
                    </el-option>
                 </el-select>
              </div>
              <div class="search-item">
                <span>学科方向：</span>
                <el-input class="dt-input" placeholder="请输入"></el-input>  
              </div>
            </div>
          </div>
          <div class="fr search-btn-box">
            <el-button class="dt-btn" @click="handleSearch"><i class="iconfont icons-chaxun"></i>查询 </el-button>
            <el-button class="dt-btn" style="margin-left: 20px;" @click="handleSearch"><i class="iconfont icons-zhongzhi"></i>重置 </el-button>
          </div>
        </div>
        <el-table
            ref="multipleTable"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            :header-cell-style="{background:'#F9FAFC',color: '#696969',textAlign: 'center'}"
            @selection-change="handleSelectionChange">
            <el-table-column
              prop="proNum"
              show-overflow-tooltip
              label="项目编号">
            </el-table-column>
            <el-table-column
              prop="proName"
              show-overflow-tooltip
              label="项目名称">
            </el-table-column>
            <el-table-column
              prop="proName"
              show-overflow-tooltip
              label="姓名">
            </el-table-column>
            <el-table-column
              prop="proName"
              show-overflow-tooltip
              label="依托单位">
            </el-table-column>
            <el-table-column
              prop="proName"
              show-overflow-tooltip
              label="项目类型">
            </el-table-column>
            <el-table-column
              prop="proName"
              show-overflow-tooltip
              label="申报编号">
            </el-table-column>
            <el-table-column
              prop="proName"
              show-overflow-tooltip
              label="学科方向">
            </el-table-column>
            <el-table-column
              prop="proName"
              show-overflow-tooltip
              label="项目状态">
            </el-table-column>
            <el-table-column
              prop="proName"
              show-overflow-tooltip
              label="评审意见">
              <template slot-scope="scope">
                  <span class="dt-txt-btn">评审</span>
              </template>
            </el-table-column>
        </el-table>
        <!-- 保存操作 -->
        <dt-dialog key="1" :isShow="dialogShow" @left-click="handleLeftClick" @right-click="dialogShow = false" title="有未保存的数据，确定继续操作？"></dt-dialog>
      </div>      
    </div>
  </dt-scroll-box>

</template>
<script>
  /**
   * 项目评审页
   * @author liujingyan 2019-02-21
   */
 
  import DtDialog from  '@/components/DtDialog'

  export default {
    name: 'projectReview',
    components: {
        DtDialog
    },
      data() {
        return {
          searchData:{

          },
          tableData:[],
          stateOptions:[],
          dialogShow: false,
          typeOptions: [],
          orgOptions: [],
          yearOptions: []         
        }
      },
      created() {
      
        this.initPage()
        this.typeOptions = [
              {
                "children": [
                    {
                        "children": [
                            {
                                "id": "921876375923593218",
                                "label": "创新工程"
                            },
                            {
                                "id": "921876375927787520",
                                "label": "院基科费"
                            },
                            {
                                "id": "921876375931981824",
                                "label": "校基科费"
                            },
                            {
                                "id": "921876375931981825",
                                "label": "改革经费"
                            }
                        ],
                        "id": "921876375923593217",
                        "label": "院校级"
                    },
                    {
                        "children": [
                            {
                                "children": [],
                                "id": "921876375931981827",
                                "label": "国家科技重大专项"
                            },
                            {
                                "children": [],
                                "id": "921876375931981828",
                                "label": "国家重点研发计划项目"
                            },
                            {
                                "children": [],
                                "id": "921876375936176128",
                                "label": "技术创新引导专项"
                            },
                            {
                                "children": [],
                                "id": "921876375936176129",
                                "label": "重大科学计划"
                            },
                            {
                                "children": [],
                                "id": "921876375936176130",
                                "label": "科技支撑计划"
                            },
                            {
                                "children": [],
                                "id": "921876375940370432",
                                "label": "973计划"
                            },
                            {
                                "children": [],
                                "id": "921876375940370433",
                                "label": "863计划"
                            },
                            {
                                "children": [],
                                "id": "921876375940370434",
                                "label": "基地和人才专项"
                            },
                            {
                                "children": [],
                                "id": "921876375940370435",
                                "label": "基础条件平台"
                            },
                            {
                                "children": [],
                                "id": "921876375940370436",
                                "label": "国际合作项目(科技部)"
                            },
                            {
                                "children": [],
                                "id": "921876375940370437",
                                "label": "其他计划项目"
                            },
                            {
                                "children": [],
                                "id": "921876375940370438",
                                "label": "国家自然科学基金项目"
                            },
                            {
                                "children": [],
                                "id": "921876375940370439",
                                "label": "国家社会科学基金项目"
                            }
                        ],
                        "id": "921876375931981826",
                        "label": "国家级"
                    },
                    {
                        "children": [
                            {
                                "children": [],
                                "id": "921876375944564737",
                                "label": "国家卫生健康委项目"
                            },
                            {
                                "children": [],
                                "id": "921876375944564738",
                                "label": "教育部项目"
                            },
                            {
                                "children": [],
                                "id": "921876375944564739",
                                "label": "国家发改委项目"
                            },
                            {
                                "children": [],
                                "id": "921876375944564740",
                                "label": "国家药监局项目"
                            },
                            {
                                "children": [],
                                "id": "921876375944564741",
                                "label": "国家中医药局项目"
                            },
                            {
                                "children": [],
                                "id": "921876375944564742",
                                "label": "中国科协"
                            },
                            {
                                "children": [],
                                "id": "921876375944564743",
                                "label": "北京市自然科学基金"
                            },
                            {
                                "children": [],
                                "id": "921876375944564744",
                                "label": "北京市教委"
                            },
                            {
                                "children": [],
                                "id": "921876375944564745",
                                "label": "北京市科协"
                            },
                            {
                                "children": [],
                                "id": "921876375948759040",
                                "label": "其他部委项目"
                            }
                        ],
                        "id": "921876375944564736",
                        "label": "省部级"
                    },
                    {
                        "children": [
                            {
                                "children": [],
                                "id": "921876375948759042",
                                "label": "其他"
                            }
                        ],
                        "id": "921876375948759041",
                        "label": "其他"
                    },
                    {
                        "children": [
                            {
                                "children": [],
                                "id": "921876375948759044",
                                "label": "与企业联合项目"
                            }
                        ],
                        "id": "921876375948759043",
                        "label": "横向"
                    }
                ],
                "id": "921876375923593216",
                "label": "科研"
            }
            ]
      },
      methods: {
        // 初始化页面 
        initPage () {
          this.$http.get('/sys-dict/codes/SRCAT+PROST')
          .then((res)=>{
            let data = res.data
            this.stateOptions = data.PROST
            this.typeOptions = data.SRCAT
            this.getTableData()

          })
          .catch((err)=>{

          })
        },
        handleSelectionChange (val) {
           this.multipleSelection = val
        },
        handleLeftClick () {

        },
        getUrl() {
          let url = ''
          for(let key in this.searchData){
            if(this.searchData[key]){
              url = url + '\/' + this.searchData[key]
            } else {
              url = url + '\/' + this.searchData[key]
            }
          }

          return this.pageType + url
        },
        getTableData () {
          this.$http.get('/srpms-outline/' + this.getUrl()+'\/'+ '1')
          .then((res)=>{
            res.data.forEach((item)=>{
              if(item.proCategory){
                let str0 = item.proCategory
                let str1 = str0.substr(1, str0.length - 2)
                item.proCategory = str1.split(',')
              }
            })
            this.tableData = res.data || []
          })
          .catch((err)=>{

          })
        },
        formatterType (val) {
          let item = this.allTypes.filter((o)=> {
            return String(o.value) === String(val) 
          })

          return item && item.length ? item[0].label:''
        },
        handleSearch () {
          // 发起请求
          this.getTableData()
        },
        // 数据改变之后保存索引值到数组中
        handleValueChange (rowIdx) {
          this.savedFlag = false
          let isInArray = this.unsavedData.findIndex((o)=>{
            return o === rowIdx
          })

          if (isInArray === -1) {
            this.unsavedData.push(rowIdx)
          }
        },
        getOrg(){
          let txt = ''
          this.orgOptions.forEach((item)=>{
            if(String(item.id) === this.titleData.orgId){
              txt = item.label
            }
          })

          return txt
        }
      },
      computed: {
        activeorgIdName () {
          let idx = this.orgOptions.findIndex((o)=>{
            return o.id === this.searchData.orgId
          })
          

          return idx !== -1 ? this.orgOptions[idx].label:''
        },
        exportTitle () {
          let year =  this.titleData.year?  this.titleData.year + '年':''
          let month = this.titleData.month? this.titleData.month + '月':''
          let orgName = this.getOrg()

          return '确定导出：'+ orgName + year + month + '的模板?'
        }
      }
  }
</script>
<style lang="less">
  .project-review-page{
    .pl80{padding-left: 80px;}
    .sub-title{
      margin: 30px 0;
      font-size: 18px;
      color: #63B4FF;
      letter-spacing: -0.27px;
      line-height: 18px;
      >span{
        display: inline-block;
        position: relative;
        top: -2px;
        width: 4px;
        height: 17px;
        margin-right: 10px;
        vertical-align: middle;
        background-color: #63B4FF;
      }
      }
    .search-item{
      margin-bottom: 20px;
      >span{
        display: inline-block;
        height: 30px;
        width: 105px;
        line-height: 30px;
        font-size: 16px;
        color: #696969;
        text-align: right;
      }

    }
    .table-item{
      display: table-cell;
    }
    .table-title{
      margin-bottom: 20px;
    }
    .el-table .cell{
      line-height: 30px;
    }
    .el-table .cell{
      text-align: center;
    }
    .opt-box{
      // margin: 40px 0 20px 14px;
    }
    .search-box{
      margin-top: 30px;
    }
    @media screen and (max-width: 1600px) {
      .dt-select,.dt-input{
        width: 150px;
      }
      .table-item{
        padding-left: 20px;
        &.pl0{padding-left: 0;}
      }
      .search-item{
        >span{
          width: 80px;
        }
      }
    }
  }
</style>


